
















import HeadTtop from '../../../../components/head/head.vue'
export default {
	components: {
		'HeadTtop': HeadTtop
	},
	data() {
		return {
			Title: '审核',
			backshow: true,
			topsrc: '../../../../static/images/back.png',
			status:'',
			state:''
		};
	},
	onLoad(e) {
		this.status = e.status
		if( this.status == 2 ){
			this.state = '审核中'
			this.txt = '您已申请退回押金，平台正在审核中。'
		}else{
			this.state = '已退还'
			this.txt = '已退还押金，注意查收'
		}
	},
	methods:{
		
	}
}
